@use '../../utils/styles/constants.scss' as var;

.expertise {
    display: flex;
    flex-direction: column;
    gap: 30px;

    .expertise-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        gap: 30px;
    
        &__left {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 5px;
    
            &__date {
                font-size: 16px;
                font-weight: var.$font-weight-400;
                line-height: 19.2px;
                color: var.$main-raven;
                text-align: left;
                width: 80px;
            }
        }
    
        &__right {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 5px;
    
            &__description {
                font-size: 14px;
                font-weight: var.$font-weight-400;
                line-height: 21px;
                color: var.$main-dark-blue;
                text-align: left;
            }
        }
    }
}

